.searchContainer{max-width:1200px;margin:80px auto 0;padding:20px;margin-bottom:30px;background-color:rgba(77,44,94,.9);border-radius:8px;box-shadow:0 4px 6px rgba(0,0,0,.1)}.searchContainer .resultLink{text-decoration:none;color:#e7e5e5;font-weight:500}.searchContainer .resultLink:hover{text-decoration:underline}@media(max-width: 768px){.searchContainer{padding:1rem;margin-top:60px}}.title{font-size:2rem;color:#fff;margin-bottom:1.5rem;text-align:center}@media(max-width: 768px){.title{font-size:1.5rem}}.loading,.error,.noResults{text-align:center;font-size:1.2rem;margin:2rem 0;color:#e7e5e5}.error{color:#ff6b6b}.resultList{list-style-type:none;padding:0}.resultItem{background-color:rgba(77,44,94,.7333333333);border-radius:4px;padding:1rem;margin-bottom:1rem;display:flex;justify-content:space-between;align-items:center;transition:transform .2s ease-in-out,background-color .3s ease}.resultItem:hover{transform:translateY(-2px);background-color:rgba(91,52,111,.7333333333)}@media(max-width: 576px){.resultItem{flex-direction:column;align-items:flex-start}}.name{font-weight:bold;color:#fff}.grade{color:#e7e5e5;font-size:.9rem}@media(max-width: 576px){.grade{margin-top:.5rem}}.searchInput{width:100%;max-width:400px;margin:0 auto 2rem;display:flex;align-items:center}.searchInput input{flex-grow:1;padding:10px;border:2px solid #fff;border-top-left-radius:12px;border-bottom-left-radius:12px;border-right:none;font-size:14px;background:rgba(255,255,255,.2);color:#fff;outline:none}.searchInput input::placeholder{color:#e7e5e5}.searchInput input:focus{background:rgba(255,255,255,.1)}.searchInput button{background-color:rgba(255,255,255,.2);border:2px solid #fff;border-top-right-radius:12px;border-bottom-right-radius:12px;border-left:none;padding:11px 18px;cursor:pointer;font-size:14px;color:#fff;transition:background-color .3s ease}.searchInput button:hover{background-color:rgba(255,255,255,.3)}@media(max-width: 430px){.searchContainer{background:rgba(0,0,0,0);padding:1rem;margin-top:80px}.title{color:rgba(77,44,94,.7333333333)}.resultItem{background-color:rgba(77,44,94,.8)}}@media(max-width: 320px){.searchContainer{margin-top:70px}.title{font-size:1.3rem}.resultItem{padding:.8rem}.name,.grade{font-size:.9rem}}